@font-face {
  font-family: "m-regular";
  src: url("../fonts/Montserrat-Regular.ttf");
}

@font-face {
  font-family: "m-bold";
  src: url("../fonts/Montserrat-Bold.ttf");
}

@font-face {
  font-family: "m-medium";
  src: url("../fonts/Montserrat-Medium.ttf");
}

@font-face {
  font-family: "m-light";
  src: url("../fonts/Montserrat-Light.ttf");
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "m-regular", sans-serif;
  text-decoration: none;
}

::selection {
  background-color: rgb(0, 59, 61);
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url("../images/code_bg.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100vh;
  background-blend-mode: darken;
  background-color: rgba(22, 22, 22, 0.8235294118);
  color: #ffffff;
}

.card {
  text-align: center;
  padding: 0 10px;
}

.card .myImg {
  max-width: 230px;
  margin: 10px auto;
}

.card .myImg img {
  width: 100%;
  border-radius: 50%;
  animation: colorAnim alternate 0.8s infinite;
  padding: 4px;
}

@keyframes colorAnim {
  0% {
    border: 3px solid rgba(0, 128, 0, 0);
  }

  100% {
    border: 3px solid rgb(44, 171, 255);
  }
}

.card .name {
  font-size: min(40px, 10vw);
  letter-spacing: 3px;
  text-shadow: 2px 2px 3px rgb(0, 0, 0);
}

.card .prof {
  margin-top: 10px;
  letter-spacing: 1.5px;
  font-size: 16px;
  text-shadow: 2px 2px 3px rgb(0, 0, 0);
  font-family: "m-medium";
}

.card .myInfo_text {
  max-width: 400px;
  margin: 25px auto auto auto;
  padding-top: 25px;
  border-top: 1px solid rgba(104, 104, 104, 0.7450980392);
  font-size: 17px;
  text-shadow: 2px 2px 3px rgb(0, 0, 0);
  line-height: 26px;
  letter-spacing: 0.6px;
}

.card .myMail {
  display: flex;
  justify-content: center;
  align-items: color;
  text-shadow: 2px 2px 3px rgb(0, 0, 0);
  gap: 0 10px;
  margin-top: 30px;
}

.card .myMail .mail_icon img {
  width: 20px;
}

.card .myMail .mail {
  letter-spacing: 1px;
  font-size: min(17px, 4vw);
}

.card .social__pages {
  display: flex;
  justify-content: center;
  max-width: 240px;
  margin: 30px auto auto auto;
}

.card .social__pages .social_icons {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px 50px;
}

.card .social__pages .social_icons a {
  max-width: 35px;
  transition: 0.2s ease;
}

.card .social__pages .social_icons a img {
  width: 100%;
}

.card .social__pages .social_icons a:nth-child(1) {
  max-width: 30px;
}